import React, { useState } from 'react';
import { Steps, Step } from 'react-step-builder';
import { Analytics } from 'analytics';
import googleTagManager from '@analytics/google-tag-manager';
import googleAnalytics from '@analytics/google-analytics';
import './widget.scss';
import config from '../../feedback-config';
import Step1 from './step1';
import Step2 from './step2';
import Step3 from './step3';
const defaultSteps = [
    {
        component: Step1,
        name: 'step1',
    },
    {
        component: Step2,
        name: 'step2',
    },
    {
        component: Step3,
        name: 'step3',
    },
];
const FeedbackWidget = ({ opened, theme, onSubmit, steps = defaultSteps, adapter, currentPath, app, adapterConfig, }) => {
    const [isOpen, setIsOpen] = useState(true);
    const [openAnimation, setOpenAnimation] = useState('entering');
    // Analytics
    // console.log('adapter config > ', adapterConfig);
    const plugins = [];
    if (adapter === 'gtm') {
        plugins.push(googleTagManager(adapterConfig));
    }
    else if (adapter === 'ga') {
        plugins.push(googleAnalytics(adapterConfig));
    }
    // console.log('Plugins  > ', plugins);
    // Do event names need to be passed in?
    // eg. Category, button click, thumbs up/down, close...
    const analytics = Analytics({
        debug: true,
        app,
        plugins,
    });
    // For testing
    // Fire function when 'track' calls happen
    // analytics.on('track', ({ payload }) => {
    //   console.log('track call just happened. Payload > ', payload);
    // });
    // track pageview
    // analytics.page()
    // WIP Values don't transfer exactly from Google.
    // trackcustom event
    const adapterEventName = 'Feedback Loaded';
    analytics.track(adapterEventName, {
        category: `Feedback ${currentPath}`,
    });
    const onClose = () => {
        setOpenAnimation('exiting');
        setIsOpen(false);
        // analytics.track('Feedback Closed', {
        //   category: `Feedback ${currentPath}`,
        // });
    };
    const themeValues = config.themes[theme].styles;
    if ((opened === undefined && !isOpen) || opened === false) {
        return null;
    }
    // if value is passed in then use that. otherwise use default.
    return (React.createElement("div", { style: themeValues.container, className: `widget docked-widget widget-${openAnimation} docked-widget-${config.themes[theme].position}` },
        React.createElement("div", { className: `widget widget-${isOpen}` },
            React.createElement("form", null,
                React.createElement(Steps, null, steps.map((s) => (React.createElement(Step, { key: s.name, component: s.component, themeValues: themeValues, onSubmit: onSubmit, handleClose: onClose, analytics: analytics, currentPath: currentPath }))))))));
};
export default FeedbackWidget;
