import React from 'react';
import ReactDOM from 'react-dom';
import Analytics from 'analytics';
import googleTagManager from '@analytics/google-tag-manager';

import Widget from './feedbackWidget';

const defaultConfig = {
  style: {
    primaryColor: 'red',
    secondaryColor: 'green',
  },
};

export default class EmbeddableWidget {
  constructor(props) {
    this.el = null;
    this.analytics = null;
    this.track.bind(this);
    this.mounted = false;
    this.props = { ...defaultConfig, ...props };
    this.mount(this.props);
  }

  mount({ parentElement = null, ...props } = {}) {
    if (!EmbeddableWidget.validate(props)) {
      throw new Error('App name and container id required');
    }
    this.generateAnalystic(props);

    const handleSubmit = (data) => {
      if (props.onSubmit) {
        props.onSubmit(data);
      } else {
        this.track(data.eventName, data.payload);
      }
    };
    // eslint-disable-next-line react/jsx-props-no-spreading
    const component = <Widget {...props} onSubmit={handleSubmit} />;

    const doRender = () => {
      if (this.el) {
        throw new Error('EmbeddableWidget is already mounted, unmount first');
      }
      const el = document.createElement('div');
      el.setAttribute('class', 'cleanslate');

      if (parentElement) {
        document.querySelector(parentElement).appendChild(el);
      } else {
        document.body.appendChild(el);
      }
      ReactDOM.render(component, el);
      this.mounted = true;
      this.el = el;
    };
    if (document.readyState === 'complete') {
      doRender();
    } else {
      window.addEventListener('load', () => {
        doRender();
      });
    }
  }

  generateAnalystic(data) {
    this.analytics = Analytics({
      app: data.app,
      plugins: [googleTagManager(data.adapterConfig)],
    });
  }

  track(eventName, payload) {
    this.analytics.track(eventName, payload);
  }

  unmount() {
    if (!this.el) {
      throw new Error('EmbeddableWidget is not mounted, mount first');
    }
    ReactDOM.unmountComponentAtNode(this.el);
    this.el.parentNode.removeChild(this.el);
    this.el = null;
    this.mounted = false;
  }

  static validate(data) {
    if (!data?.app || !data?.adapterConfig?.containerId) {
      return false;
    }
    return true;
  }

  open() {
    if (this.mounted) {
      this.unmount();
      this.mount({ ...this.props, opened: true });
    }
  }

  close() {
    if (this.mounted) {
      this.unmount();
      this.mount({ ...this.props, opened: false });
    }
  }
}
