import React, { useCallback } from 'react';
import config from '../../feedback-config';
import { convertToDashString } from './helper';
const labels = config.labels.en;
const Step1 = ({ next, themeValues, onSubmit, analytics, currentPath, }) => {
    const submitEntry = useCallback((answer) => {
        if (onSubmit) {
            onSubmit({
                eventName: convertToDashString(labels.step1Title),
                payload: answer,
            });
        }
        const adapterEventName = 'Feedback step one';
        analytics.track(adapterEventName, {
            category: `Feedback ${currentPath}`,
            // action: answer,
            label: answer,
        });
        next();
    }, [next, onSubmit, labels.step1Title]);
    return (React.createElement("div", { className: "widget-helpful form-group" },
        React.createElement("div", { style: themeValues.step1header, className: "widget-header" },
            React.createElement("div", { className: "widget-header-title" }, labels.step1Title),
            React.createElement("button", { type: "button", style: themeValues.yesIcon, className: "widget-header-icon icon-yes", 
                // If yes should we just close widget?
                onClick: () => {
                    submitEntry('yes');
                } },
                React.createElement("span", null)),
            React.createElement("button", { type: "button", style: themeValues.noIcon, className: "widget-header-icon icon-no", onClick: () => {
                    submitEntry('no');
                } },
                React.createElement("span", null)))));
};
export default Step1;
